@media screen and (max-width: 800px) {
  .container-padre__proyectos {
    height: 115vw;
    padding-top: 3vw;
    background-color: #2a2a2a;
  }

  .style-h1__proyectos {
    font-size: 8vw;
    margin-top: 12%;
  }

  .title-proyectos__lineaHorizontal {
    width: 20vw !important;
    height: 0.3vw !important;
    left: -10vw;
    top: -1.2vw;
  }

  .style-h3-subtitulo__proyectos {
    font-size: 3.2vw;
    margin-top: 0;
  }

  .container-proyectos {
    display: none;
  }

  .style-a__project:hover:before {
    height: 0;
  }

  .style-a__project:hover .style-span__text-tech {
    height: 5.2vw;
    padding: 0 5px;
    padding-right: 7px;
    padding-top: 1px;
    margin-left: 4.8px;
    margin-top: 5.5px;
    font-size: 3vw;
    box-shadow: 0 0 5px #fff;
    transition: all 0.7s;
  }

  .style-a__project .fa-eye {
    font-size: 0 !important;
  }

  .carousel {
    display: block;
    width: 85%;
    margin: auto;
    margin-top: 13%;
  }

  .caousel-item {
    width: auto;
    height: 350px;
  }

  .carousel-inner {
    width: 100%;
    height: 450px;
  }

  .style-a__project {
    display: flex;
    flex-direction: column;
  }

  .style-a__project:hover:before {
    opacity: 0;
  }

  .style-image__product {
    box-shadow: 0 0 10px #000;
  }

  .container__text-tech__project {
    width: 80%;
    height: 23%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    bottom: -10px;
    padding-left: 35px;
  }

  .project-2,
  .project-3 {
    padding-left: 20px;
  }

  .style-span__text-tech {
    width: auto;
    height: 5.4vw;
    padding: 2px 5px;
    padding-right: 7px;
    margin-left: 1.5%;
    margin-top: 1.5%;
    background-color: #474747;
    font-size: 3vw;
    font-weight: 600;
    color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 7px #000;
  }

  .carousel-control-prev,
  .carousel-control-next {
    height: 202px !important;
    z-index: 0;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    border-radius: 50%;
    position: relative;
    bottom: 5%;
  }

  .carousel-control-prev-icon {
    right: 79% !important;
  }

  .carousel-control-next-icon {
    left: 79% !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 768px) {
  .title-proyectos__lineaHorizontal {
    left: -9vw;
    top: -0.3vw;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    position: relative;
    top: 7.5vw !important;
  }

  .project-2,
  .project-3 {
    padding-left: 10%;
  }
}

@media only screen and (min-width: 320px) and (max-width: 320px) {
  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    position: relative;
    bottom: 10.5vw !important;
  }

  .style-a__project .project-1 {
    padding-left: 8% !important;
  }

  .style-a__project .project-2,
  .style-a__project .project-3 {
    padding-left: 4% !important;
  }
}

@media only screen and (min-width: 375px) and (max-width: 425px) {
  .style-a__project .project-1 {
    padding-left: 7% !important;
  }
}
